import './index.css'
import state, {subscribe} from './redux/state'
import React from 'react'
import ReactDOM from 'react-dom'
import './index.css'
import * as serviceWorker from './serviceWorker'
import App from './App'
import {addPost, RootStateType, updateNewMessageText, updateNewPostText, addMessage} from './redux/state'
import {BrowserRouter} from 'react-router-dom'


export let rerenderEntireThree = (state: RootStateType) => {
    ReactDOM.render(
        <BrowserRouter>
            <App state={state} addPost={addPost} updateNewPostText={updateNewPostText}
                 addMessage={addMessage} updateNewMessageText={updateNewMessageText}/>
        </BrowserRouter>,
        document.getElementById('root'))
}
rerenderEntireThree(state)
subscribe(rerenderEntireThree)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
